/* Multistep form
=============================== */

.j-pro fieldset {
    border: none;
    outline: none;
    margin: 0;
    padding: 0;
    position: absolute;
    opacity: 0;
    left: -9999px;
    top: 0;
    -webkit-transform: translateY(-4%);
    -moz-transform: translateY(-4%);
    -ms-transform: translateY(-4%);
    -o-transform: translateY(-4%);
    transform: translateY(-4%);
    -webkit-transition: opacity.3s, -webkit-transform.3s;
    -moz-transition: opacity.3s, -moz-transform.3s;
    -ms-transition: opacity.3s, -ms-transform.3s;
    -o-transition: opacity.3s, -o-transform.3s;
    transition: opacity.3s, transform.3s;
}

.j-pro .j-steps {
    border: 1px solid rgba(0, 0, 0, .12);
    -webkit-border-radius: 3px;
    -moz-border-radius: 3px;
    -o-border-radius: 3px;
    border-radius: 3px;
    margin-bottom: 25px;
    text-align: center;
    -webkit-transition: all.3s;
    -moz-transition: all.3s;
    -ms-transition: all.3s;
    -o-transition: all.3s;
    transition: all.3s;
}

.j-pro .j-active-fieldset {
    left: 0;
    position: relative;
    opacity: 1;
    -webkit-transform: translateY(0);
    -moz-transform: translateY(0);
    -ms-transform: translateY(0);
    -o-transform: translateY(0);
    transform: translateY(0);
}

.j-pro fieldset .j-unit,
.j-pro fieldset .j-row {
    display: none;
}

.j-pro .j-active-fieldset .j-unit,
.j-pro .j-active-fieldset .j-row {
    display: block;
}

.j-pro .j-steps p {
    color: rgba(0, 0, 0, .56);
    font-size: 16px;
    height: 36px;
    line-height: 36px;
    margin: 0;
    padding: 0;
}

.j-pro .j-steps span {
    color: rgba(0, 0, 0, .56);
    font-size: 13px;
    height: 13px;
    line-height: 13px;
}

.j-pro .j-active-step .j-steps p {
    color: rgba(0, 0, 0, .87);
}

.j-pro .j-active-step .j-steps {
    background-color: #e8eaf6;
    border: 1px solid #303f9f;
}

.j-pro .j-passed-step .j-steps {
